.certificates {
  margin-top: 138px;
}

.certificates__wrap {
  margin-top: 18px;
  position: relative;
  padding-top: 80px;
}
.certificate-item {
  width: 100%;
  position: relative;
  &:nth-child(1) {
    .certificate-item__inner {
      top: -70px;
    }
  }

  &:nth-child(2) {
    .certificate-item__inner {
      top: -188px;
    }
  }

  &:nth-child(3) {
    .certificate-item__inner {
      top: -306px;
    }
  }
}

.certificate-item__inner {
  transition: all 1s ease;
  position: absolute;
  top: 0;
  left: 50%;
  z-index: -1;
  transform: translateX(-50%);
  opacity: 0;
}

.certificate-item__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  line-height: 138%;
  text-align: center;
  letter-spacing: 0.03em;
  color: var(--color-text);
  opacity: 0.4;
  transition: all 1s ease;
  cursor: pointer;
  margin-bottom: 63px;
  position: relative;
  &:hover {
    opacity: 1;
    + .certificate-item__inner {
      opacity: 1;
    }

    ~ .certificate-item__side-text {
      opacity: 1;
    }
  }
}

.certificate-item__side-text {
  transition: all 1s ease;
  position: absolute;
  left: 0;
  text-align: center;
  top: 50%;
  width: 300px;
  transform: translateY(-50%) rotate(270deg);
  opacity: 0;
}
.certificate-item__inner_img {
  width: 316px;
}

@media screen and (max-width: 1024px) {
  .certificate-item__title {
    font-size: 32px;
  }
  .certificate-item:nth-child(2) .certificate-item__inner {
    top: -178px;
  }
  .certificate-item:nth-child(3) .certificate-item__inner {
    top: -286px;
  }

  .certificate-item__side-text {
    left: -100px;
  }
}

@media screen and (max-width: 768px) {
  .certificates {
    margin-top: 90px;
  }

  .certificates__wrap {
    padding-top: 90px;
  }

  .certificate-item__side-text {
    transform: translateY(0) rotate(360deg);
    left: 120px;
    top: 50px;
    text-align: left;
  }

  .certificate-item__title {
    font-size: 26px;
  }
}

@media screen and (max-width: 550px) {
  .certificates {
    margin-top: 60px;
  }
}

@media screen and (max-width: 500px) {
  .certificate-item__title {
    font-size: 22px;
  }
  .certificate-item__side-text {
    left: 50%;
    transform: translateX(-50%);
    top: 40px;
    text-align: center;
    font-size: 14px;
  }

  .certificate-item__inner_img {
    width: 290px;
  }

  .certificate-item:nth-child(2) .certificate-item__inner {
    top: -163px;
  }

  .certificate-item:nth-child(3) .certificate-item__inner {
    top: -256px;
  }
}

@media screen and (max-width: 425px) {
  .certificate-item__title {
    font-size: 16px;
  }

  .certificate-item__side-text {
    font-size: 12px;
    top: 30px;
  }

  .certificate-item:nth-child(2) .certificate-item__inner {
    top: -155px;
  }

  .certificate-item:nth-child(3) .certificate-item__inner {
    top: -240px;
  }

  .certificate-item:nth-child(1) {
    .certificate-item__side-text {
      top: -93px;
    }
  }

  .certificate-item:nth-child(2) {
    .certificate-item__side-text {
      top: -178px;
    }
  }

  .certificate-item:nth-child(3) {
    .certificate-item__side-text {
      top: -263px;
    }
  }
}
