.articles {
  margin-top: 210px;
}

.articles-title {
  //margin-top: 126px;
}

.articles__wrap {
  margin-top: 60px;

  .swiper-container {
    padding-top: 10px;
  }
}

.articles-slide_cart {
  max-width: 524px;
  width: 100%;
  padding: 36px 36px 24px 36px;
  background: #f8f8f8;
  position: relative;
  z-index: 2;
  margin-top: 125px;
  transition: opacity, margin-top 1s ease;

  img {
    height: 298px;
    display: block;
  }
}

.articles-slide__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
  margin-top: 16px;
  transition: all 2s ease;
  opacity: 0;
}

.articles-slide__text {
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  letter-spacing: 0.025em;
  color: var(--color-text);
  margin-top: 14px;
  transition: all 2s ease;
  opacity: 0;
}

.articles-slide__btn {
  display: inline-block;
  margin-top: 40px;
  transition: all 2s ease;
  opacity: 0;
}

.articles-slider__slide {
  transition: all 1s ease;
  opacity: 0.4;
  position: relative;
  max-width: 615px;
  height: 580px;
}

.article-slide__bg {
  width: 511px;
  height: 509px;
  max-width: 511px;
  position: absolute;
  top: 40px;
  z-index: 1;
  opacity: 0;
  transition: opacity 1s ease;
}

.article-slide__bg_img {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.9);

  img {
    height: 100%;
    object-fit: cover;
    filter: blur(30px);
  }
}

.article-slide__bg_text {
  position: absolute;
  right: -39px;
  z-index: 3;
  transform: rotate(270deg);
  top: 124px;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 155%;
  letter-spacing: 0.05em;
  color: #ffffff;
}

.articles__arrows {
  max-width: 902px;
  width: 100%;
  top: 314px;
  display: flex;
  z-index: 3;
  align-items: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  justify-content: space-between;
}

.articles-slider__slide.swiper-slide-active {
  opacity: 1;
  max-height: none;

  .articles-slide_cart {
    max-width: 524px;
    margin-top: 0;
    width: 100%;
    //padding: 36px 36px 24px 36px;
    transform: translate(-45px, -10px);
    background: #f8f8f8;
    position: relative;
    z-index: 2;

    img {
      height: 298px;
    }
  }

  .article-slide__bg {
    opacity: 1;
    left: 60px;
    top: 69px;
  }

  .articles-slide__title {
    opacity: 1;
  }

  .articles-slide__text {
    opacity: 1;
  }

  .articles-slide__btn {
    opacity: 1;
  }
}

@media screen and (max-width: 1200px) {
  .articles {
    margin-top: 60px;
  }
  .articles__wrap .swiper-container {
    padding-top: 10px;
  }
  .articles__arrows {
    max-width: 800px;
    top: 214px;
  }

  .article-slide__bg {
    width: 371px;
    height: 419px;
  }

  .articles-slider__slide.swiper-slide-active .articles-slide_cart {
    //transform: translateX(100px);
    max-width: 410px;
    margin: 0 auto;
  }

  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 411px;
  }

  .articles-slide_cart {
    padding: 20px;
  }
  .articles-slider__slide.swiper-slide-active .articles-slide_cart img {
    height: 230px;
    object-fit: cover;
  }
  .articles-slide__title {
    margin-top: 10px;
  }

  .articles-slider__slide {
    height: 500px;
    max-width: none;
  }

  .article-slide__bg_text {
    right: -64px;
    top: 100px;
  }

  .articles__wrap {
    margin-top: 30px;
  }

  .articles-slide__btn {
    margin-top: 20px;
  }
}

@media screen and (max-width: 1024px) {
  .articles-slide__wrap {
    position: relative;
    max-width: 500px;
    margin: 0 auto;
  }
  .articles__arrows {
    max-width: 760px;
  }
  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 80px;
  }
}

@media screen and (max-width: 768px) {
  .articles {
    margin-top: 90px;
  }
  .articles__arrows {
    max-width: 80%;
    top: 220px;
  }
  .article-slide__bg {
    max-width: 450px;
    height: 430px;
  }

  .articles-slide__btn {
    margin-top: 20px;
  }

  .articles-slide_cart {
  }

  .articles-slider__slide {
    img {
      object-fit: cover;
    }
  }
  .articles-slider__slide.swiper-slide-active .articles-slide_cart {
    transform: translate(-30px, -10px);
  }

  .articles-slider__slide.swiper-slide-active .articles-slide_cart img {
    object-fit: cover;
    height: 200px;
  }

  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 96px;
    top: 30px;
    height: 400px;
  }

  .article-slide__bg_text {
    right: -64px;
  }
  .articles-slide__title {
    font-size: 18px;
  }

  .articles-slide__text {
    font-size: 12px;
    margin-top: 5px;
  }

  .articles__wrap {
    margin-top: 30px;
  }

  .articles-slider__slide {
    height: auto;
  }
}

@media screen and (max-width: 700px) {
  .articles-slide_cart {
    max-width: 300px;
    padding: 20px;
  }

  .article-slide__bg {
    max-width: 300px;
    height: 400px;
  }

  .articles__wrap .swiper-container {
    padding-bottom: 0;
  }

  .articles-slider__slide.swiper-slide-active .articles-slide_cart {
    transform: translateX(-30px);
  }

  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 170px;
  }
  .articles__arrows {
    top: auto;
    bottom: 120px;
  }
}

@media screen and (max-width: 550px) {
  .articles-slider__slide.swiper-slide-active .articles-slide_cart {
    transform: translateX(-20px);
  }

  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 140px;
    height: 392px;
    width: 370px;
  }

  .articles-slide__wrap {
    height: 500px;
  }

  .articles-slide_cart {
    margin-top: 0;
    max-width: 410px;
  }
  .articles-slide_cart img {
    height: 200px;
  }
  .articles-slide__wrap {
    max-width: 425px;
  }

  .article-slide__bg {
    transition: height 1s ease;
    right: 30px;
    height: 0;
    width: 0;
  }

  .articles__arrows {
    bottom: 0;
  }
}

@media screen and (max-width: 475px) {
  .articles-slider__slide.swiper-slide-active .articles-slide_cart {
    max-width: none;
  }
  .articles-slide__wrap {
    max-width: 320px;
  }
  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 43px;
  }
}

@media screen and (max-width: 375px) {
  .articles-slider__slide {
    //max-width: 290px;
  }

  .articles-slide__wrap {
    max-width: 300px;
  }

  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 32px;
  }

  .articles-slider__slide.swiper-slide-active .articles-slide_cart {
    transform: translateX(-20px);
  }

  .articles-slide__title {
    margin-top: 10px;
  }

  .articles-slide__text {
    font-size: 10px;
  }

  .article-slide__bg {
    max-width: 290px;
    height: 380px;
  }

  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    height: 370px;
  }

  .article-slide__bg_text {
    right: -53px;
    top: 80px;
  }

  .articles-slide__btn {
    opacity: 1;
    width: 100%;
    text-align: center;
    padding: 9px;
    line-height: 100%;
    font-size: 12px;
  }

  .article-slide__bg_text {
    font-size: 12px;
  }

  .articles-slide__btn {
    margin-top: 15px;
  }
}

@media screen and (max-width: 375px) {
  .articles-slide__wrap {
    max-width: 270px;
  }

  .articles-slider__slide.swiper-slide-active .articles-slide_cart {
    transform: translateX(-15px);
  }

  .articles-slider__slide.swiper-slide-active .article-slide__bg {
    left: 19px;
    opacity: 1;
    max-width: 270px;
    height: 370px;
    opacity: 1;
    top: 30px;
  }

  .article-slide__bg_text {
    right: -56px;
    top: 80px;
  }

  .articles__arrows {
    bottom: 40px;
  }

  .article-slide__bg {
    max-width: 270px;
    height: 370px;
    left: 19px;
    opacity: 1;
    width: 100%;
    top: 30px;
  }
}
