.experience {
  margin-top: 138px;
  background: url('../img/gr.png') cover;
  position: relative;
}

.shadow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  &-l {
    left: 0;
    z-index: 2;
  }
  &-r {
    right: 0;
    z-index: 2;
    transform: rotate(180deg) translateY(50%);
  }
}

.swiper-button-next:after,
.swiper-button-prev:after {
  display: none;
}
.experience-arrows {
  position: static;
  max-width: 218px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 96px;
}
.experience-button-next {
  position: static;
  margin: 0;
  border: 1px solid #d8c49f;
  border-radius: 50%;
  width: 84px;
  height: 84px;
  position: relative;
  transition: all 0.3s ease;
  &:before {
    content: '';
    position: absolute;
    width: 29px;
    height: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: url('../img/slider-arr-r.svg');
  }
  &:hover {
    background: var(--color-accent);
  }
}
.experience-button-prev {
  position: static;
  margin: 0;
  border: 1px solid #d8c49f;
  border-radius: 50%;
  width: 84px;
  height: 84px;
  position: relative;
  transition: all 0.3s ease;
  &:before {
    content: '';
    position: absolute;
    width: 28px;
    height: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-image: url('../img/slider-arr-l.svg');
  }
  &:hover {
    background: var(--color-accent);
  }
}
.experience-slider {
  margin-top: 60px;
}
.experience-slide {
  max-width: 524px;
}
.experience-slide__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  max-width: 250px;
  color: var(--color-text);
  margin-left: 181px;
}
.experience-slide__dates {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: 138%;
  margin-top: 2px;
  color: var(--color-accent);
}
.experience-slide__subtext {
  margin-top: 68px;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  letter-spacing: 0.025em;
  text-align: center;
  color: var(--color-text);
}
.swiper-button-next.swiper-button-white {
}
.swiper-button-prev.swiper-button-white {
}
.experience__wrap {
}

@media screen and (max-width: 1200px) {
  .experience-slide__title {
    font-size: 18px;
  }

  .experience-slide__dates {
    font-size: 80px;
  }
  .experience-slide__subtext {
    margin-top: 40px;
  }

  .experience-arrows {
    margin-top: 50px;
    max-width: 190px;
  }

  .experience-button-prev {
    width: 65px;
    height: 65px;
  }

  .experience-button-next {
    width: 65px;
    height: 65px;
  }
}

@media screen and (max-width: 1024px) {
  .experience-slide__title {
    margin: 0 auto;
    text-align: center;
  }

  .experience-slide__subtext {
    margin-top: 20px;
  }

  .experience-arrows {
    margin-top: 30px;
  }

  .shadow {
    display: none;
  }

  .experience-slide__dates {
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .experience {
    margin-top: 90px;
  }

  .experience-button-prev {
    width: 40px;
    height: 40px;
    &::before {
      background-size: cover;
      width: 16px;
      height: 15px;
    }
  }

  .experience-button-next {
    width: 40px;
    height: 40px;
    &::before {
      background-size: cover;
      width: 16px;
      height: 15px;
    }
  }

  .experience-arrows {
    max-width: 140px;
  }
}

@media screen and (max-width: 550px) {
    .experience {
      margin-top: 60px;
    }
}

@media screen and (max-width: 500px) {
  .experience-slide__title {
    font-size: 16px;
  }

  .experience-slide__dates {
    font-size: 58px;
  }

  .experience-slide__subtext {
    font-size: 12px;
    margin-top: 10px;
  }

  .experience-slider {
    margin-top: 40px;
  }
}
