.about {
  //margin-top: 138px;
  margin-top: 75px;

  > .container {
    position: relative;
  }
}

.title-main.about-title {
}

.about__wrap {
  margin-top: 60px;
  position: relative;
}

.about-image {
  width: 100%;
  max-width: 550px;
  margin: 0 auto;
  position: relative;

  img {
    border-radius: 50%;
    padding: 13px;
    border: 1px solid var(--color-accent);
  }
}

.about-image__title {
  font-family: var(--font-arsenal);
  font-weight: 300;
  position: absolute;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  line-height: 138%;
  text-align: center;
  letter-spacing: 0.03em;
  top: 314px;
  z-index: 1;
  color: #ffffff;
}

.about-image__btn {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 396px;
}

.about-item {
  position: absolute;
  z-index: 2;

  &.about-item-tl {
    top: 0;
    left: 0;
    max-width: 260px;
  }

  &.about-item-bl {
    bottom: 0;
    left: 0;
    max-width: 260px;
  }

  &.about-item-br {
    bottom: 0;
    right: 0;
    max-width: 260px;
    text-align: right;
  }
}

.about-item__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
}

.about-item__text {
  margin-top: 12px;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  letter-spacing: 0.025em;
  color: var(--color-text);
}

.about-item__link {
  display: block;
  margin-top: 17px;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 155%;
  letter-spacing: 0.05em;
  color: var(--color-purple);
  transition: all 0.3s ease;

  &:hover {
    text-decoration: underline;
  }
}

@media screen and (max-width: 1200px) {
  .about-image {
    max-width: 470px;
  }
  .about-image__title {
    font-size: 32px;
    top: 280px;
  }

  .about-image__btn {
    top: 350px;
  }

  .about-item {
    &.about-item-tl {
      left: 15px;
    }

    &.about-item-bl {
      left: 15px;
    }

    &.about-item-br {
      right: 15px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .about {
    margin-top: 60px;
  }
}

@media screen and (max-width: 960px) {
  .about {
    margin-top: 0;

    > .container {
      display: flex;
      flex-direction: column-reverse;
    }
  }
  .about-item {
    position: static;
    max-width: none;
    margin-top: 25px;

    &.about-item-tl {
      left: 15px;
      max-width: none;
      text-align: center;
    }

    &.about-item-bl {
      left: 15px;
      max-width: none;
      text-align: center;
    }

    &.about-item-br {
      right: 15px;
      max-width: none;
      text-align: center;
    }
  }

  .about-image {
    max-width: 290px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

@media screen and (max-width: 960px) {
  .about__wrap {
    margin-top: 30px;
  }

  .about-image__title {
    position: static;
    margin-top: 20px;
    max-width: none;
    width: 100%;
    transform: none;
    font-size: 36px;
    text-align: center;
    left: auto;
    color: var(--color-text);
  }

  .about-image__btn {
    position: static;
    margin: 0 auto;

    display: inline-block;

    transform: none;
    margin-top: 20px;
  }
}

@media screen and (max-width: 960px) {
  .about-image__title {
    font-size: 26px;
  }

  .about-image__btn {
    margin-top: 10px;
    display: inline-block;
  }

  .about-item__title {
    font-size: 18px;
  }

  .about-item__text {
    font-size: 12px;
    margin-top: 5px;
  }

  .about-item__link {
    font-size: 12px;
    margin-top: 5px;
  }
}

@media screen and (max-width: 768px) {
  .about {
    margin-top: 180px;
  }
}

@media screen and (max-width: 550px) {
  .about {
    margin-top: 60px;
  }
}
