.header {
  padding: 36px 0;
  position: relative;
  z-index: 4;
  transition: all 0.5s ease;
}

.burger {
  display: none;
}

.header__btn-mob {
  display: none;
}

.header__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__ul {
  display: flex;
  align-items: center;
}

.header__li {
  margin-right: 54px;
  font-weight: 500;
  font-size: 14px;
  transition: all 0.3s ease;
  line-height: 138%;
  color: var(--color-text);
  position: relative;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    color: var(--color-accent);
  }

  &.active-li {
    &::before {
      content: '';
      position: absolute;
      height: 1px;
      width: 100%;
      background-color: var(--color-accent);
      left: 0;
      bottom: -5px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    padding: 15px 0;
    position: fixed;
    width: 100%;
    top: 0;
    background: none;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding: 37px 0 10px;
  }

  .header__ul {
    position: fixed;
    height: 100vh;
    right: -100%;
    background-color: #fff;
    z-index: 5;
    top: 0;
    max-width: 176px;
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    padding: 37px 16px 34px 32px;
    transition: all 0.3s ease;
    &.active {
      right: 0;
    }
  }

  .header__btn-mob {
    display: block;
    position: absolute;
    bottom: 34px;
  }

  .header__li {
    margin-right: 0;
    margin-bottom: 16px;
  }

  .header__right {
    display: none;
  }

  .burger {
    display: block;
    z-index: 4;
    width: 28px;
    height: 18px;
    position: relative;
    &:before,
    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 2px;
      background-color: var(--color-accent);
      border-radius: 2px;
      left: 0;
    }

    &:before {
      top: 0;
    }

    &:after {
      bottom: 0;
    }

    span {
      position: absolute;
      content: '';
      height: 2px;
      top: 8px;
      background-color: var(--color-accent);
      border-radius: 2px;
      right: 0;
      width: 18px;
    }
  }
}
