.contacts {
  margin-top: -98px;
}

.contacts__wrap {
  margin-top: 102px;
  justify-content: space-between;
  display: flex;
}

.contacts-info {
  max-width: 524px;
  width: 100%;
}

.contacts-item__title {
  padding-left: 40px;
  position: relative;
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
  margin-bottom: 30px;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &-address {
    &:before {
      background-image: url('../img/icon-address.svg');
      width: 23px;
      height: 26px;
      top: 3px;
    }
  }

  &-phone {
    &:before {
      background-image: url('../img/icon-phone.svg');
      width: 20px;
      height: 30px;
      top: 2px;
    }
  }

  &-email {
    &:before {
      background-image: url('../img/icon-email.svg');
      width: 22px;
      height: 18px;
      top: 8px;
    }
  }
}

.contacts-item__row {
  display: flex;

  .contacts-item:first-child {
    margin-right: 149px;
  }
}

.contacts-item__text {
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  letter-spacing: 0.025em;
  color: var(--color-text);
  display: block;
}

.contacts-decoration {
  width: 100%;
  height: 1px;
  background: #e9e9e9;
  margin: 92px 0 85px;
}

.contacts-form {
  max-width: 341px;
  width: 100%;
  margin-top: -10px;

  form {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 42px;
  }

  input,
  textarea {
    border: 1px solid var(--color-accent);
    padding: 12px 17px;
    font-family: var(--font-manrope);
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 147%;
    letter-spacing: 0.025em;
    color: var(--color-text);
    //opacity: 0.4;
  }

  textarea {
    height: 97px;
    max-width: 100%;
  }

  input {
    margin-bottom: 20px;
  }

  button {
    display: inline;

    width: 125px;
  }
}

.contacts-form__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
}

.input-name {
  &.active {
    border: 1px solid #e14f4f;
  }
}

.input-email {
  &.active {
    border: 1px solid #e14f4f;
  }
}

.input-tel {
  &.active {
    border: 1px solid #e14f4f;
  }
}

.input-text {
  &.active {
    border: 1px solid #e14f4f;
  }
}

.form-bot {
  margin-top: 36px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-message {
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  max-width: 159px;
  letter-spacing: 0.025em;
  color: #e14f4f;
}
@media screen and (max-width: 1200px) {
  .contacts {
    margin-top: 90px;
  }
}

@media screen and (max-width: 1024px) {
  .contacts-decoration {
    width: 100%;
    height: 1px;
    background: #e9e9e9;
    margin: 40px 0;
  }
}

@media screen and (max-width: 940px) {
  .contacts-info {
    max-width: 49%;
  }
  .contacts-decoration {
    margin: 30px 0;
  }
  .contacts-item__row {
    justify-content: space-between;
  }
  .contacts-item__row .contacts-item:first-child {
    margin-right: 0;
  }
}

@media screen and (max-width: 768px) {
  .contacts {
    margin-top: -40px;
  }

  .contacts__wrap {
    margin-top: 30px;
    flex-direction: column;
  }

  .contacts-info {
    max-width: 70%;
    margin: 0 auto 0;
  }

  .contacts-form {
    max-width: 70%;
    margin: 60px auto 0;
  }

  .contacts-form form {
    margin-top: 20px;
  }

  .contacts-item__title {
    font-size: 18px;
  }

  .contacts-item__text {
    font-size: 12px;
  }
}

@media screen and (max-width: 550px) {
  .contacts {
    margin-top: 60px;
  }
  .contacts-info {
    max-width: 100%;
    margin: 0 auto 0;
  }
  .contacts-form {
    max-width: 100%;
    margin: 60px auto 0;
  }
}

@media screen and (max-width: 425px) {
  .contacts__wrap {
    margin-top: 30px;
  }
  .contacts-decoration {
    display: none;
  }
  .contacts-item__row {
    flex-direction: column;
  }

  .contacts-item {
    margin-bottom: 30px;
  }

  .contacts-form {
    margin: 30px auto 0;
  }
}

@media screen and (max-width: 375px) {
  .contacts {
    margin-top: 20px;
  }
}
