.posts {
  margin-top: 67px;
}


.posts-banner {
  margin-top: 65px;
}

.posts-banner__subtitle {
  margin-top: 13px;
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
}


.posts__wrap {
  margin-top: 84px;
  display: flex;
  justify-content: space-between;
}

.posts-last {
  max-width: 615px;
  width: 100%;
}

.posts-last__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
  margin-bottom: 34px;
}

.post-item {
  display: flex;
  margin-bottom: 55px;
}

.post-item__image {
  width: 250px;
  height: 270px;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.post-item__info {
  width: 341px;
  margin-left: 23px;
}


.post-item__date {
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  letter-spacing: 0.025em;
  color: var(--color-text);
  opacity: 0.3;
}

.post-item__title {
  font-family: var(--font-manrope);
  margin-top: 7px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: var(--color-text);
}

.post-item__text {
  font-family: var(--font-manrope);
  margin-top: 24px;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 156%;
  letter-spacing: 0.05em;
  color: var(--color-text);

}

.post-popular {
  max-width: 341px;
  width: 100%;
}

.post-popular__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
  margin-bottom: 34px;
}

.post-popular__item {
  display: flex;
  margin-bottom: 36px;
}

.post-popular__img {
  width: 67px;
  height: 67px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.post-popular__text {
  max-width: 250px;
  margin-left: 24px;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 155%;
  letter-spacing: 0.05em;
  color: var(--color-text);
}

@media screen and (max-width: 1024px) {
  .posts {
    margin-top: 100px;
  }

  .posts-last {
    max-width: 500px;
  }

  .post-item__image {
    height: 200px;
  }

  .post-item__text {
    margin-top: 10px;
    font-size: 14px;
  }

  .post-popular__item {
    margin-bottom: 30px;
  }

  .post-popular__text {
    margin-left: 20px;
    max-width: 200px;
  }
}


@media screen and (max-width: 900px) {
  .posts__wrap {
    flex-direction: column;
    margin-top: 60px;
  }

  .posts-last {
    max-width: none;
  }

  .post-item__image {
    width: 200px;
    height: 200px;
  }
  .post-popular {
    max-width: none;

  }

  .post-popular__item_wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .post-popular__item {
    max-width: 360px;
    width: 100%;
  }
}


@media screen and (max-width: 500px) {
  .posts-banner {
    margin-top: 35px;
  }
  .posts-banner__subtitle {
    font-size: 18px;
    margin-top: 5px;
  }

  .posts-last__title {
    text-align: center;
  }

  .post-popular {
    margin-top: 20px;
  }

  .post-item__image {
    width: 120px;
    height: 120px;
  }

  .post-item__info {
    width: 290px;
    margin-left: 15px;
  }

  .post-item__date {
    font-size: 12px;
  }

  .post-item__title {
    font-size: 16px;
  }

  .post-item__text {
    font-size: 12px;
    margin-top: 6px;
  }

  .post-popular__title {
    text-align: center;
  }

  .post-popular__item {
    max-width: none;
  }


}

@media screen and (max-width: 425px) {
  .post-item {
    flex-direction: column;
  }

  .post-item__image {
    width: 100%;
    height: 200px;
  }

  .post-item__info {
    margin-left: 0;
    width: auto;
    margin-top: 15px;
  }
}