.article-page {
  margin-top: 68px;
}

.article-page__title {
  text-align: left;
}

.article-page__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 31px;
}

.article-page__top_date {
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  letter-spacing: 0.025em;
  color: var(--color-text);
  opacity: 0.4;
}

.article-page__top_author {
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 147%;
  letter-spacing: 0.025em;
  color: var(--color-text);
}

.article-page__image {
  margin-top: 13px;
}

.article-page__content {
  margin-left: 183px;
  margin-top: 48px;
}

.article-page__h3 {
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: var(--color-text);
  max-width: 663px;
}

.article-page__h4 {
  margin-top: 47px;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  max-width: 663px;
  line-height: 155%;
  letter-spacing: 0.05em;
  color: var(--color-text);
}

.article-page__p {
  max-width: 663px;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  color: var(--color-text);
  margin-top: 20px;
}


.last-articles {
  margin-top: 85px;
}

.last-articles__title {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 128.5%;
  color: var(--color-text);
}

.last-articles__wrap {
  margin-top: 33px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.last-articles__item {
  max-width: 341px;

  p {
    font-family: var(--font-manrope);
    margin-top: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 155%;
    letter-spacing: 0.05em;
    color: var(--color-text);

  }
}


@media screen and (max-width: 1200px) {
  .article-page {
    margin-top: 120px;
  }

  .article-page__content {
    margin-left: 100px;
    margin-top: 30px;
  }

  .article-page__h3 {
    font-size: 18px;
  }

  .article-page__h4 {
    font-size: 18px;
  }

  .article-page__p {
    font-size: 15px;
    line-height: 22px;
  }

  .last-articles__wrap {
    align-items: baseline;
    margin-top: 20px;
  }

  .last-articles__item {
    max-width: 32%;
    p {
      font-size: 12px;
      margin-top: 5px;
    }
  }
}

@media screen and (max-width: 768px) {
  .article-page__title {
    font-size: 36px;
  }

  .article-page__top {
    margin-top: 20px;
  }

  .article-page {
    margin-top: 100px;
  }
  .article-page__content {
    margin-left: 0;
    margin-top: 20px;
  }

  .article-page__h3 {
    margin-top: 20px;
    font-size: 18px;
    max-width: none;
  }

  .article-page__h4 {
    max-width: none;
    font-size: 16px;
    margin-top: 40px;
  }

  .article-page__p {
    max-width: none;
    font-size: 14px;
    margin-top: 15px;
  }

  .last-articles {
    margin-top: 40px;
  }
}

@media screen and (max-width: 550px) {

  .article-page__image {
    height: 250px;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .article-page__top {
    flex-direction: column;
    align-items: flex-start;
  }

  .article-page__top_author {
    margin-top: 5px;
  }

  .last-articles__title {
    text-align: center;
  }
  .article-page__h3 {
    font-size: 16px;
  }

  .article-page__h4 {
    font-size: 16px;
    margin-top: 30px;
  }
  .article-page__p {
    font-size: 14px;
    margin-top: 10px;
  }

  .last-articles__wrap {
    flex-direction: column;
    max-width: 350px;
    margin: 30px auto 0;
  }

  .last-articles__item {
    max-width: none;
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}