@import '../../node_modules/normalize.css/normalize';

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

body {
  font-family: var(--font-manrope);
}

.container {
  max-width: 1100px;
  padding: 0 15px;
  margin: 0 auto;
}

.container-relative {
  position: relative;
}

.fp-tableCell {
  //height: auto !important;
}

button {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
  display: block;
}

img {
  width: 100%;
}

section {
  height: 100vh;
}

.btn {
  font-family: var(--font-manrope);
  font-weight: 500;
  font-size: 14px;
  line-height: 138%;
  color: var(--color-text);
  padding: 11px 16px;
  border: 1px solid var(--color-accent);
  transition: all 0.3s ease;

  &:hover {
    border: 1px solid var(--color-text);
  }

  &:active {
    background-color: var(--color-accent);
    border: 1px solid var(--color-accent);
  }
}

.btn-bd {
  font-family: var(--font-manrope);
  font-weight: 500;
  font-size: 14px;
  background-color: var(--color-accent);
  border: 1px solid var(--color-accent);
  line-height: 138%;
  padding: 11px 16px;
  color: var(--color-text);
  outline: 1px solid #fff;
  // outline: 1px solid transparent;
  outline-offset: -2px;
  transition: all 0.3s ease;
  &:hover {
    border: 1px solid var(--color-text);
  }

  &:active {
    background-color: var(--color-accent);
    border: 1px solid var(--color-accent);
    outline: none;
  }
}

a {
  display: block;
}

.overflow {
  position: fixed;
  z-index: 3;
  top: 0;
  left: auto;
  right: -100%;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #d8c49f;
  opacity: 0.3;
  transition: all 0.3s ease;
  &.active {
    right: 0;
  }
}

.title-main {
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: 400;
  font-size: 43px;
  line-height: 100%;
  text-align: center;
  color: var(--color-text);
}
[class*='ymaps-2'][class*='-ground-pane'] {
  filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
  /* Firefox 3.5+ */
  -webkit-filter: grayscale(100%);
  /* Chrome 19+ & Safari 6+ */
}

.main-slider-btn-next,
.main-slider-btn-prev {
  width: 84px;
  display: block;
  height: 84px;
  border-radius: 50%;
  border: 1px solid var(--color-accent);
  position: relative;
  transition: all 0.3s ease;
  margin: 0;
  padding: 0;
  &:hover {
    background-color: var(--color-accent);
  }

  &:before {
    content: '';
    position: absolute;
    background-image: url('../img/slider-arr-l.svg');
    width: 28px;
    height: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.main-slider-btn-prev:before {
  background-image: url('../img/slider-arr-r.svg');
}

@media screen and (max-width: 1200px) {
  .main-slider-btn-next,
  .main-slider-btn-prev {
    width: 65px;
    height: 65px;
  }
}

@media screen and (max-width: 1024px) {
  .main-slider-btn-next,
  .main-slider-btn-prev {
    width: 65px;
    height: 65px;
    transform: none;
  }
}

@media screen and (max-width: 768px) {
  .main-slider-btn-next,
  .main-slider-btn-prev {
    width: 40px;
    height: 40px;
    &:before {
      background-size: cover;
      width: 16px;
      height: 15px;
    }
  }
}
@media screen and (max-width: 500px) {
  .title-main {
    font-size: 36px;
  }
}
