.services {
  margin-top: 138px;
}

h2.title-main.services-main {
}

.services__wrap {
  position: relative;
  .swiper-container {
    padding-top: 115px;
  }
}

.services-slider__slide {
  max-width: 706px;
  position: relative;
  //overflow: visible;
  &:before {
    content: '';
    position: absolute;
    width: 110%;
    height: 100%;
    left: -5px;
    top: 0;
    transition: all 1s ease;
    background-color: rgba(255,255,255, .9);
    opacity: 1;
  }

  &.swiper-slide-active {
    &:before {
      opacity: 0;
    }
    .services-slider__slide_title {
      opacity: 1;
    }

    .services-slider__slide_info {
      opacity: 1;
    }
  }


}

.services__arrows {
  position: absolute;
  max-width: 1002px;
  height: 84px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 248px;
  z-index: 2;
  left: 50%;
  transform: translateX(-50%);
}

.services-slider__slide_title {
  position: absolute;
  font-family: var(--font-arsenal);
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  line-height: 138%;
  top: -60px;
  left: 90px;
  letter-spacing: 0.03em;
  color: var(--color-accent);
  max-width: 476px;
  transition: all 1s ease;
  opacity: 0;
}

.services-slider__slide_info {
  max-width: 500px;
  margin: 0 auto;
  padding-top: 36px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  transition: all .3s ease;
  opacity: 0;
  p {
    display: block;
    max-width: 220px;
    width: 100%;
    font-family: var(--font-manrope);
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 147%;
    letter-spacing: 0.025em;
    color: var(--color-text);
    margin-bottom: 17px;
  }
}

@media screen and (max-width: 1200px) {
  .services {
    margin-top: 230px;
  }
  .services-slider__slide {
    max-width: 600px;
  }

  .services__wrap .swiper-container {
    padding-top: 75px;
  }

  .services__arrows {
    top: 178px;
  }
}

@media screen and (max-width: 1150px) {
  .services-slider__slide {
    max-width: 656px;
  }

  .services__arrows {
    position: absolute;
    max-width: 830px;
  }
}

@media screen and (max-width: 1024px) {
  .services-slider__slide {
    max-width: 500px;
  }

  .services-slider__slide_title {
    font-size: 28px;
    top: -45px;
    left: 0;
    max-width: 100%;
  }

  .services__arrows {
    top: 150px;
    max-width: 650px;
  }
}

@media screen and (max-width: 768px) {
  .services-slider__slide_title {
    max-width: none;
    font-size: 28px;
    left: 0;
  }

  .services__arrows {
    top: 110px;
    max-width: 520px;
  }

  .services-slider__slide_info {
    padding-top: 15px;
    p {
      font-size: 12px;
      margin-bottom: 10px;
    }
  }

  .services__wrap .swiper-container {
    padding-top: 60px;
  }

  .services-slider__slide {
    max-width: 425px;
  }
}

@media screen and (max-width: 550px) {
  .services__wrap .swiper-container  {
    padding-top: 60px;
  }
  .services-slider__slide {
    max-width: 290px;
  }

  .services__arrows {
    top: 70px;
    max-width: 390px;
  }

  .services-slider__slide_title {
    max-width: none;
    font-size: 18px;
    left: 0;
    top: -30px;
  }
}

@media screen and (max-width: 425px) {
  .services-slider__slide {
    max-width: 220px;
  }
  .services-slider__slide_title {
    top: -25px;
    left: 10px;
  }

  .services-slider__slide_info {
    padding-top: 10px;
    p {
      margin-bottom: 5px;
    }
  }

  .services__arrows {
    top: 43px;
    max-width: 320px;
  }
}

@media screen and (max-width: 320px) {
  .services-slider__slide {
    max-width: 200px;
  }

  .services__arrows {
    top: 43px;
    max-width: 280px;
  }
}
