.hero {
  position: relative;
  margin-top: -92px;
}

.hero-title {
  font-family: 'Arsenal', sans-serif !important;
  color: var(--color-text);
  width: 100%;
  position: absolute;
  text-align: center;
  font-weight: normal;
  font-size: 170px;
  line-height: 119.5%;
  letter-spacing: 0.055em;
  z-index: 2;
  top: 34px;
}

.hero__wrap {
  max-width: 703px;
  margin: 0 auto;
  position: relative;
}

.hero__wrap_title {
  position: absolute;
  font-family: 'Arsenal', sans-serif !important;
  color: var(--color-accent);
  font-weight: normal;
  font-size: 43px;
  line-height: 100%;
  top: 307px;
  left: -185px;
  max-width: 578px;
}

.hero__wrap_subtitle {
  position: absolute;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 150%;
  letter-spacing: 0.025em;
  color: #ffffff;
  max-width: 274px;
  top: 422px;
  left: 90px;
}

.hero-btn {
  position: absolute;
  top: 518px;
  left: 90px;
  padding: 11px 22px;
}

@media screen and (max-width: 1200px) {
  .hero {
    margin-top: 73px;
  }
  .hero-title {
    font-size: 140px;
    top: -20px;
  }

  .hero__wrap_title {
    left: -90px;
  }

  .hero__wrap_subtitle {
    left: 60px;
  }

  .hero-btn {
    left: 60px;
  }
}

@media screen and (max-width: 1024px) {
  .hero {
    height: auto;
  }

  .hero-title {
    font-size: 110px;
    top: -80px;
  }

  .hero__wrap {
    max-width: 580px;
    margin-top: 120px;
  }

  .hero__wrap_title {
    top: 237px;
    font-size: 32px;
    left: -70px;
    line-height: 36px;
  }

  .hero__wrap_subtitle {
    top: 350px;
  }

  .hero-btn {
    top: 430px;
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .hero__wrap_subtitle {
    left: 0;
    color: var(--color-text);
  }
  .hero__wrap_title {
    font-size: 19px;
    top: 42%;
    transform: translate(-50%, -50%);
  }
  .hero-title {
    font-size: 48px;
    top: -35px;
  }

  .hero-btn {
    left: 0;
    width: 100%;
  }

  .hero__wrap {
    max-width: 350px;
    margin-top: 60px;
  }

  .hero__wrap_title {
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
  }
}

@media screen and (max-width: 600px) {
  .hero-title {
    font-size: 48px;
    line-height: 117%;
    top: -35px;
  }

  .hero__wrap {
    display: flex;
    flex-direction: column;
  }

  .hero-image {
    order: 1;
  }

  .hero__wrap_title {
    font-size: 32px;

    line-height: 117%;
  }

  .hero__wrap_subtitle {
    position: static;
    color: var(--color-text);
    order: 2;
    max-width: none;
    margin-top: 10px;
  }

  .hero-btn {
    position: static;
    order: 3;
    margin-top: 10px;
  }
}

@media screen and (max-width: 550px) {
  .hero__wrap {
    margin-top: 110px;
  }
}

@media screen and (max-width: 320px) {
  .hero__wrap_title {
    font-size: 19px;
    line-height: 30px;
    top: 38%;
    transform: translate(-50%, -50%);
  }
}
