.footer {
  margin-top: auto;
  .fp-tableCell {
    vertical-align: baseline !important;
  }
}

.footer-st {
  margin-top: 138px;
  #map {
    position: static !important;
  }

  .footer-inner {
    position: static;
  }
}

#map {
  position: absolute;
  bottom: 322px;
}

.footer-inner {
  background: #1d110a;
  position: absolute;
  width: 100%;
  bottom: 0;
}
.footer-wrap {
  background: #1d110a;
  display: flex;
  padding-top: 79px;
  justify-content: space-between;
}

.footer__logo {
  width: 75px;
  height: 75px;
}

.footer__nav {
}

.footer__ul {
  display: flex;
  flex-wrap: wrap;
  max-width: 372px;
  justify-content: space-between;
  margin-left: 86px;
  //width: 100%;
}

.footer__li {
  width: 186px;
  text-align: left;
  font-family: var(--font-manrope);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 138%;
  color: #ffffff;
  margin-bottom: 24px;
  transition: all 0.3s ease;
  &:hover {
    color: var(--color-accent);
  }
}

.footer-socials {
  display: flex;
  align-items: center;
}

.footer-soc {
  display: block;
  margin-right: 21px;
  &:last-child {
    margin-right: 0;
  }
}

.footer-bottom {
  background: #1d110a;
  padding: 10px 0 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  p {
    font-family: var(--font-manrope);
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 147%;
    letter-spacing: 0.025em;
    color: #ffffff;
    &:nth-child(2) {
      margin-left: auto;
      margin-right: 21px;
    }
  }

  img {
    display: block;
  }
}
@media screen and (max-width: 1200px) {
  .footer {
    margin-top: 90px;
  }
  .footer-inner {
    position: static;
  }
  #map {
    position: static;
  }
}

@media screen and (max-width: 1024px) {
  .footer-wrap {
    padding-top: 40px;
  }

  .footer-bottom {
    padding: 10px 0 40px;
  }
}

@media screen and (max-width: 768px) {
  .footer-wrap {
    padding-top: 30px;
  }

  .footer-bottom {
    padding: 10px 0 30px;
  }
  .footer__ul {
    margin-left: 30px;
  }

  .footer__li {
    font-size: 14px;
    width: 145px;
    margin-bottom: 14px;
  }
}

@media screen and (max-width: 550px) {
  .footer {
    margin-top: 60px;
  }
}

@media screen and (max-width: 500px) {
  .footer-wrap {
    flex-direction: column;
    padding-top: 10px;
  }

  .footer__ul {
    max-width: none;
    // flex-direction: column;
    margin-left: 0;
    flex-wrap: wrap;
  }

  .footer__li {
    display: block;
    width: 49%;
    max-width: none;
    margin-bottom: 10px;
  }

  .footer__left {
    order: 1;
    width: 100%;
    margin: 20px auto;
  }

  .footer__logo {
    display: none;
  }

  .footer__nav {
    order: 2;
    margin-top: 22px;
  }

  .footer-socials {
    justify-content: flex-start;
  }

  .footer-soc {
    width: 24px;
    height: 24px;
    margin-right: 16px;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .footer-bottom {
    max-width: 250px;
    margin: 8px auto 0;
    padding-top: 0;
    padding-bottom: 30px;
    flex-wrap: wrap;
    justify-content: center;
    a {
      margin-top: 20px;
      margin-left: 20px;
    }
    p {
      &:nth-child(1) {
        margin-top: 12px;
        order: 3;
        text-align: center;
        letter-spacing: 0.025em;
        color: #615853;

        padding-left: 34px;
        position: relative;
        &::before {
          top: -5px;
          content: '';
          position: absolute;
          left: -7px;
          background-image: url('../img/logo.svg');
          background-size: cover;
          width: 28px;
          height: 28px;
        }
      }
    }
  }
  .footer-bottom p:nth-child(2) {
    margin: 0;
    margin-top: 20px;
  }
}
